.sermons{
    padding-bottom: 0;
}

.sermon-overview{
    display: flex;
    margin-bottom: 15px;
    position: relative;

    &::before{
        content: '';
        display: block;
        position: absolute;
        width: 100px;
        height: 100px;
        background: #3c72fc;
        top: 30px;
        left: -30px;
        z-index: 5;
    }

    &::after{
        content: '';
        width: 0;
        height: 0;
        position: absolute;
        border-left: 16px solid #fff;
        border-top: 8px solid transparent;
        border-bottom: 8px solid transparent;
        top: 70px;
        left: 15px;
        z-index: 6;
    }

    .details{
        order: 2;
        width: 35%;
        padding: 0 0 0 70px;

        h2{
            line-height: 1.15em;
        }
    }

    .video-embed{
        iframe{
            max-width: 100%;
        }
    }

    .link-images{
        order: 1;
        position: relative;
        padding-bottom: 50px;
        width: 75%;

        figure{
            width: 100%;
            height: auto;
            margin: 0;

            &.main-image{
                width: 75%;
                max-height: 550px;
                overflow: hidden;
            }

            &.sub-image{
                width: 450px;
                position: absolute;
                bottom: 0;
                right: 0;
                border-top: 15px solid #fff;
                border-left: 15px solid #fff;
                overflow: hidden;
            }
            
            img{
                vertical-align: middle;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

.recent-sermons{
    display: flex;
    width: 100%;
    position: relative;
    bottom: -85px;

    .sermon{
        width: 100%;
        padding: 50px 60px;
        background: #fff;
        box-shadow: 10px 0 60px rgba(0, 0, 0, .08);
    }

    .date{
        padding: 15px;
        color: #3c72fc;
        font-size: 24px;
        @include Jost;
        background: #ebf1ff;
        float: left;
        margin-right: 40px;
    }

    h3{
        text-transform: uppercase;
        font-size: 20px;
        @include Jost-Black;
        margin-bottom: 5px;
        color: #0f0d1d;
        overflow: hidden;
    }

    p{
        font-size: 16px;
        color: #726f84;
        line-height: 32px;
        overflow: hidden;
        margin: 0;
    }
}