/*
SCSS Build File


General Layout
*/
@import url("https://fonts.googleapis.com/css2?family=Jost:wght@300;400;600;900&display=swap");
html, body {
  height: 100%;
  min-height: 100%;
  background: #0f0d1d;
  margin: 0;
  padding: 0;
  font-family: "Jost", sans-serif;
  position: relative;
  z-index: -1;
}

.page-content {
  width: 1340px;
  max-width: calc(100% - 80px);
  margin: 0 auto;
  position: relative;
}

.sr-only {
  width: 0;
  height: 0;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  display: block;
}

/*

Header Styles

*/
header {
  position: absolute;
  display: flex;
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  height: 115px;
  box-sizing: border-box;
  top: 0;
  width: 100%;
}

.logo {
  width: 240px;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
  margin: 0;
}
.logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

nav.primary {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
nav.primary ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: flex-start;
}
nav.primary ul.navigation {
  width: 100%;
  padding: 0 40px;
}
nav.primary ul.navigation a::after {
  content: "";
  position: absolute;
  height: 2px;
  background: #fff;
  width: 0;
  bottom: -5px;
  left: 0;
  border-radius: 2px;
  transition: all 0.33s ease-in-out;
}
nav.primary ul.navigation a:hover::after {
  width: 100%;
}
nav.primary ul.navigation a.active::after {
  width: 15px;
}
nav.primary ul.navigation a.active:hover::after {
  width: 100%;
}
nav.primary ul.social-media {
  padding-right: 25px;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
}
nav.primary ul.social-media li {
  margin-right: 0;
}
nav.primary ul.social-media a {
  width: 26px;
  height: 100%;
  display: block;
  background: transparent no-repeat center;
  background-size: 14px;
}
nav.primary ul.social-media a.facebook {
  background-image: url("/assets/dist/images/facebook.png");
}
nav.primary ul.social-media a.youtube {
  background-image: url("/assets/dist/images/youtube-brands.svg");
}
nav.primary li {
  color: #fff;
  font-size: 18px;
  line-height: 115px;
  margin-right: 30px;
}
nav.primary a {
  color: #fff;
  text-decoration: none;
  position: relative;
}

.quick-contact {
  width: 320px;
  color: #fff;
  padding: 0px 25px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #e7e7e7;
}
.quick-contact a {
  color: #fff;
  text-decoration: none;
}

.qc-text {
  color: #fff;
  opacity: 0.8;
}

/*

Banner Styles
*/
:root {
  --nav-height: 115px;
}

.page-banner {
  position: relative;
  z-index: -1;
  background: transparent;
  height: calc(100vh - var(--nav-height));
  padding-top: var(--nav-height);
  background: #0f0d1d url("/assets/dist/images/banner-background.png") no-repeat 100% top;
  background-size: cover;
}
.page-banner .page-banner-content {
  width: 100%;
  max-width: calc(100% - 120px);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: calc(100% - var(--nav-height));
}
@media all and (min-width: 720px) {
  .page-banner .page-banner-content {
    max-width: calc(100% - 480px);
  }
}
.page-banner h1 {
  font-family: "Jost", sans-serif;
  font-weight: 900;
  font-size: 90px;
  text-indent: 17px;
  letter-spacing: -0.002rem;
  text-transform: uppercase;
  color: #fff;
  width: 100%;
  text-align: left;
  margin: 0;
}
.page-banner .btn {
  margin-top: 40px;
  align-self: flex-start;
}

.mouse {
  background: #4e5559 linear-gradient(transparent 0%, transparent 50%, #ffffff 50%, #ffffff 100%);
  position: relative;
  width: 26px;
  height: 44px;
  border-radius: 100px;
  background-size: 100% 200%;
  opacity: 0.5;
  margin: 0 auto;
  transition: all 0.33s ease-out;
  animation: colorSlide 15s linear infinite, nudgeMouse 15s ease-out infinite;
}
.mouse:before, .mouse:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
.mouse:before {
  width: 23px;
  height: 41px;
  background-color: #222a30;
  border-radius: 100px;
}
.mouse:after {
  background-color: #ffffff;
  width: 5px;
  height: 5px;
  border-radius: 100%;
  animation: trackBallSlide 15s linear infinite;
}
.mouse:hover, .mouse:focus {
  opacity: 1;
  cursor: pointer;
}

@keyframes colorSlide {
  0% {
    background-position: 0% 100%;
  }
  20% {
    background-position: 0% 0%;
  }
  21% {
    background-color: #4e5559;
  }
  29.99% {
    background-color: #ffffff;
    background-position: 0% 0%;
  }
  30% {
    background-color: #4e5559;
    background-position: 0% 100%;
  }
  50% {
    background-position: 0% 0%;
  }
  51% {
    background-color: #4e5559;
  }
  59% {
    background-color: #ffffff;
    background-position: 0% 0%;
  }
  60% {
    background-color: #4e5559;
    background-position: 0% 100%;
  }
  80% {
    background-position: 0% 0%;
  }
  81% {
    background-color: #4e5559;
  }
  90%, 100% {
    background-color: #ffffff;
  }
}
@keyframes trackBallSlide {
  0% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
  6% {
    opacity: 1;
    transform: scale(0.9) translateY(2.5px);
  }
  14% {
    opacity: 0;
    transform: scale(0.2) translateY(20px);
  }
  15%, 19% {
    opacity: 0;
    transform: scale(0.2) translateY(-10px);
  }
  28%, 29.99% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
  30% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
  36% {
    opacity: 1;
    transform: scale(0.9) translateY(2.5px);
  }
  44% {
    opacity: 0;
    transform: scale(0.2) translateY(20px);
  }
  45%, 49% {
    opacity: 0;
    transform: scale(0.2) translateY(-10px);
  }
  58%, 59.99% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
  60% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
  66% {
    opacity: 1;
    transform: scale(0.9) translateY(2.5px);
  }
  74% {
    opacity: 0;
    transform: scale(0.2) translateY(20px);
  }
  75%, 79% {
    opacity: 0;
    transform: scale(0.2) translateY(-10px);
  }
  88%, 100% {
    opacity: 1;
    transform: scale(1) translateY(-10px);
  }
}
@keyframes nudgeMouse {
  0% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(8px);
  }
  30% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(8px);
  }
  60% {
    transform: translateY(0);
  }
  80% {
    transform: translateY(8px);
  }
  90% {
    transform: translateY(0);
  }
}
@keyframes nudgeText {
  0% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(2px);
  }
  30% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(2px);
  }
  60% {
    transform: translateY(0);
  }
  80% {
    transform: translateY(2px);
  }
  90% {
    transform: translateY(0);
  }
}
@keyframes colorText {
  21% {
    color: #4e5559;
  }
  30% {
    color: #ffffff;
  }
  51% {
    color: #4e5559;
  }
  60% {
    color: #ffffff;
  }
  81% {
    color: #4e5559;
  }
  90% {
    color: #ffffff;
  }
}
/*


Content Styles

*/
.btn {
  font-family: "Jost", sans-serif;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  background: #000;
  display: inline-block;
  width: auto;
  padding: 20px 40px;
  text-decoration: none;
  letter-spacing: 0.05rem;
  transition: all 0.33s ease-in-out;
}
.btn.btn-primary {
  background: #960018;
}
.btn:hover, .btn:focus {
  background: #fff;
  color: #000;
}
.btn.btn-secondary {
  background: #fff;
  color: #0f0d1d;
}
.btn.btn-secondary:hover, .btn.btn-secondary:focus {
  color: #fff;
  background: #0f0d1d;
}

section {
  padding: 100px 0 150px;
}
section .section-content {
  width: 100%;
  max-width: calc(100% - 120px);
  margin: 0 auto;
}
@media all and (min-width: 720px) {
  section .section-content {
    max-width: calc(100% - 480px);
  }
}
section.light-section {
  background: #fff;
}
section.light-section h2,
section.light-section h3 {
  color: #0f0d1d;
}
section.light-section p {
  color: #222;
}
section.gray-section {
  background: #f2f4f8;
}
section.gray-section h2,
section.gray-section h3 {
  color: #0f0d1d;
}
section.gray-section p {
  color: #222;
}
section h2,
section h3 {
  font-family: "Jost", sans-serif;
  font-weight: 900;
  color: #fff;
  text-transform: uppercase;
  font-size: 50px;
  letter-spacing: -0.002rem;
  margin: 0 0 10px 0;
}
section .callout-text {
  font-family: "Jost", sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: #8f8da0;
}
section .scripture-reference {
  color: #fff;
  font-size: 12px;
  text-decoration: none;
  font-weight: bold;
  vertical-align: super;
}
section p {
  color: #f2f4f8;
}

.sermons {
  padding-bottom: 0;
}

.sermon-overview {
  display: flex;
  margin-bottom: 15px;
  position: relative;
}
.sermon-overview::before {
  content: "";
  display: block;
  position: absolute;
  width: 100px;
  height: 100px;
  background: #3c72fc;
  top: 30px;
  left: -30px;
  z-index: 5;
}
.sermon-overview::after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  border-left: 16px solid #fff;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  top: 70px;
  left: 15px;
  z-index: 6;
}
.sermon-overview .details {
  order: 2;
  width: 35%;
  padding: 0 0 0 70px;
}
.sermon-overview .details h2 {
  line-height: 1.15em;
}
.sermon-overview .video-embed iframe {
  max-width: 100%;
}
.sermon-overview .link-images {
  order: 1;
  position: relative;
  padding-bottom: 50px;
  width: 75%;
}
.sermon-overview .link-images figure {
  width: 100%;
  height: auto;
  margin: 0;
}
.sermon-overview .link-images figure.main-image {
  width: 75%;
  max-height: 550px;
  overflow: hidden;
}
.sermon-overview .link-images figure.sub-image {
  width: 450px;
  position: absolute;
  bottom: 0;
  right: 0;
  border-top: 15px solid #fff;
  border-left: 15px solid #fff;
  overflow: hidden;
}
.sermon-overview .link-images figure img {
  vertical-align: middle;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.recent-sermons {
  display: flex;
  width: 100%;
  position: relative;
  bottom: -85px;
}
.recent-sermons .sermon {
  width: 100%;
  padding: 50px 60px;
  background: #fff;
  box-shadow: 10px 0 60px rgba(0, 0, 0, 0.08);
}
.recent-sermons .date {
  padding: 15px;
  color: #3c72fc;
  font-size: 24px;
  font-family: "Jost", sans-serif;
  font-weight: 300;
  background: #ebf1ff;
  float: left;
  margin-right: 40px;
}
.recent-sermons h3 {
  text-transform: uppercase;
  font-size: 20px;
  font-family: "Jost", sans-serif;
  font-weight: 900;
  margin-bottom: 5px;
  color: #0f0d1d;
  overflow: hidden;
}
.recent-sermons p {
  font-size: 16px;
  color: #726f84;
  line-height: 32px;
  overflow: hidden;
  margin: 0;
}

.events {
  padding-top: 185px;
  padding-bottom: 100px;
  background-image: url("/assets/dist/images/wavy-lines.png");
  background-repeat: no-repeat;
  background-position: top left;
  background-size: 25% auto;
}
.events h2,
.events .callout-text {
  width: 100%;
  text-align: center;
}
.events .callout-text {
  margin-bottom: 50px;
}
.events ul {
  display: flex;
  flex-wrap: wrap;
}
.events ul li {
  width: calc(33% - 30px);
  margin-right: 45px;
}
.events ul li:nth-child(3n) {
  margin-right: 0;
}
.events figure {
  margin: 0;
}
.events figure img {
  width: calc(100% - 40px);
}

.event-details {
  background: #fff;
  margin-left: 30px;
  top: -80px;
  position: relative;
  padding: 20px 30px;
  height: 250px;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.25);
}
.event-details .date {
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;
  background: #3c72fc;
  padding: 5px 20px;
  position: absolute;
  top: -28px;
  font-weight: 600;
  left: 0;
}
.event-details h3 a {
  color: #0f0d1d;
  text-decoration: none;
  font-size: 28px;
  text-transform: uppercase;
  font-family: "Jost";
  letter-spacing: -0.02rem;
}
.event-details p {
  color: #726f84;
  font-size: 18px;
  line-height: 2rem;
}
.event-details > a {
  position: absolute;
  bottom: 30px;
  width: calc(100% - 80px);
  color: #0f0d1d;
  text-decoration: none;
}
.event-details > a:after {
  content: "";
  width: 40px;
  height: 3px;
  background: #0f0d1d;
  display: block;
  border-radius: 5px;
  position: absolute;
  bottom: -12px;
}

.callout-section {
  width: 100%;
  background: transparent url("/assets/dist/images/Callout-Background.png") no-repeat center center;
  background-size: cover;
  padding: 70px 0;
}
.callout-section .section-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.callout-section h2 {
  font-family: "Jost", sans-serif;
  font-weight: 900;
  color: #fff;
  font-size: 40px;
  text-transform: uppercase;
  letter-spacing: -0.02rem;
  max-width: 50%;
}

/*

Footer Styles

*/
footer {
  background: #2c353c;
  color: #fff;
  padding-bottom: 150px;
}
footer .footer-content {
  width: 100%;
  max-width: calc(100% - 120px);
  margin: 0 auto;
}
@media all and (min-width: 720px) {
  footer .footer-content {
    max-width: calc(100% - 480px);
  }
}
footer .logo {
  border: 0;
  margin: 0;
  padding: 60px 0 30px;
}
footer .contact {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
footer .contact p {
  font-style: italic;
  max-width: 350px;
  text-align: left;
  margin: 0 0 0 50px;
}
footer .contact p a {
  float: right;
}
footer .contact a {
  color: #fff;
  display: block;
  text-decoration: none;
  font-weight: 600;
}
footer .contact hr {
  width: 100%;
  border: 0;
  background: rgba(255, 255, 255, 0.05);
  height: 2px;
  margin: 20px 0 40px;
}

.newsletter-signup {
  margin-left: auto;
  margin-right: 0;
}
.newsletter-signup > div {
  background: #0f0d1d;
  padding: 25px 30px;
  border-radius: 5px;
  margin-left: 60px;
  position: relative;
}
.newsletter-signup > div h2 {
  font-size: 16px;
  font-weight: normal;
  margin: 0 0 15px;
}

.mc-field-group {
  position: relative;
  height: 44px;
}
.mc-field-group label {
  position: absolute;
  top: 53%;
  left: 15px;
  color: #000;
  z-index: 1;
  transform: translateY(-50%);
  pointer-events: none;
  opacity: 1;
}
.signup-active .mc-field-group label {
  opacity: 0;
}
.mc-field-group .email {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.optionalParent {
  height: 10px;
}
.optionalParent input[type=submit] {
  border: 0;
  background: red;
  color: #fff;
  font-weight: bold;
  position: relative;
  top: -37px;
  left: calc(100% - 76px);
  height: 36px;
  border-radius: 2px;
}

.brandingLogo a {
  float: none;
  position: absolute;
  bottom: -55px;
  right: 0;
}

/*

Vendor Overrides

*/
.vcVerseLink {
  color: #f2f4f8;
  text-decoration: none;
}
.vcVerseLink:hover, .vcVerseLink:focus {
  color: #960018;
}
.light-section .vcVerseLink, .gray-section .vcVerseLink {
  color: #0f0d1d;
}
.light-section .vcVerseLink:hover, .light-section .vcVerseLink:focus, .gray-section .vcVerseLink:hover, .gray-section .vcVerseLink:focus {
  color: #960018;
}